.editForm__textInputContainer {
  width: 100%;
  height: 25px;
  position: relative;
  input[type="date"],
  input[type="time"] {
    font-size: 1.5rem;
    width: 100%;
    @include breakpointFirst {
      font-size: 2rem;
    }
    &::-webkit-calendar-picker-indicator,
    &::-webkit-calendar-picker-indicator {
      position: absolute;
      right: 0;
    }
  }
}

.editForm__textInput {
  width: 100%;
  font-size: 2rem;
  font-family: $font-family-Open-Sans;
  background-color: rgba(0, 0, 0, 0);
  border: 0;
  border-bottom: 2px solid $color-neutral-secondary-dark;
  caret-color: $color-black;
  position: relative;
  z-index: 1;
  overflow-x: auto;
  &--solvent {
    color: transparent;
  }
}

.editForm__printSolvent {
  position: absolute;
  top: 0;
}

.editForm__error {
  color: $color-red;
  font-size: $font-size-small;
  line-height: $font-size-small;
  height: $font-size-small;
  margin-top: 0.3rem;
}
